<template>
  <div id="pano" ref="pano">
     <noscript>
       <table style="width:100%;height:100%;">
         <tr style="vertical-align:middle;">
           <td>
             <div style="text-align:center;">
               ERROR:<br/><br/>Javascript not activated<br/><br/>
             </div>
           </td>
         </tr>
       </table>
     </noscript>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data(){
    return {
      krpano:null,//krpano实例
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.embedpano()
       window.changePanos = this.changePanos
       window.showDialog = this.showDialog
       document.addEventListener('fullscreenchange', this.handleFullscreenChange)
       //初始化全局设置
       this.changePanos(this.currentScene)
       this.xz(this.rotate)
       this.bgMusicChange(this.bgMusic)
    })
  },
  computed: {
  ...mapGetters(["currentScene",'fullscreen','rotate','vr','bgMusic','viewState'])
  },
  watch: {
    //场景
    currentScene: {
      handler(scene) {
        this.changePanos(scene)
      },
      // immediate: true
    },
    //全屏
    fullscreen: {
      handler(val) {  
        this.qp(val)
      },
    },
    //旋转
    rotate: {
      handler(val) {
        this.xz(val) 
      },
    },
    //vr
    vr: {
      handler(val) {
        this.vrchange(val) 
      },
    },
    bgMusic:{
      handler(val) {
        this.bgMusicChange(val) 
      },
    },
    viewState:{
      handler(val) {
        this.viewChange(val) 
      },
    },
  },
  methods: {
    embedpano() {
      embedpano({
        xml: "./vtour/tour.xml",
        target: "pano", 
        html5: "auto",
        mobilescale: 1.0,
        passQueryParameters: true,
        vars: {
          "plugin[vtoureditor].keep":true //保留操作状态
        },
        onready: (krpano_interface)=>{
          this.krpano = krpano_interface;
          // 初始化进入增城广场
          // this.krpano.call("loadpano('./panos/增城广场/pano.xml', null, MERGE, BLEND(0.5));");
        },
      });
    },
    //场景切换
    changePanos(path){
      if(this.currentScene!=path){
        this.$store.dispatch('globalEvent/setScene', path)
      }
      this.krpano.call(`loadscene(${path}, null, MERGE, 2);`);
      // this.krpano.call("loadpano('./panos/" + path + "/pano.xml');");
    },
    //全屏
    qp(flag){
      if(flag){
       this.krpano.set("fullscreen", flag);
      }
    },
    handleFullscreenChange() {
     if(!document.fullscreenElement) {
        // 退出全屏 
       this.$store.dispatch('globalEvent/qp')
     }
    },
    //旋转
    xz(val){
     this.krpano.set("autorotate.enabled", val);
    },
    vrchange(val){
      this.krpano.call("webvr.enterVR();")
    },
    bgMusicChange(val){
      if(val){
       this.krpano.call("playsound(bgsnd, './vtour/assets/medium/bgMusic.mp3', 0);")
      }else{
       this.krpano.call("stopsound(bgsnd);")
      }
    },
    viewChange(val){
       this.krpano.call(val)
    },
    showDialog(type,val,title){
      //val：纯文字，纯图片 （1到多张，数组），纯视频，html, 组件（组件名）
      const regex = /^\[([^\]]+)\]$/;
      let valData
      if(regex.test(val)) {
        // 字符串数组
       const arr = val.match(regex)[1].split(',');
       valData=arr
      } else {
        // 字符串
       valData=val
      }
      let obj={
        val:valData,
        type,title
      }
      this.$store.dispatch('globalEvent/showDialog', obj)
    }
  }
}
</script>

<style>
#pano {
  width: 100%;
  height: 100%;
}
</style>
