<template>
  <div class="globalTools">
    <!-- 全屏 -->
    <el-tooltip effect="dark" content="全屏" placement="left">
     <div class="globalTools_item qp" @click="qp()"></div>
    </el-tooltip>
    <!-- 旋转 -->
    <el-tooltip effect="dark" content="旋转" placement="left">
     <div class="globalTools_item xz" @click="xz()"></div>
    </el-tooltip>
    <!-- vr -->
    <el-tooltip effect="dark" content="vr" placement="left">
     <div class="globalTools_item vr" @click="vr()"></div>
    </el-tooltip>
    <!-- 视角切换 -->
    <el-tooltip effect="dark" content="视角切换" placement="left">
     <div class="globalTools_item sjqh" @click="dialogVisible=true"></div>
    </el-tooltip>
    <!-- 音乐 -->
    <el-tooltip effect="dark" content="音乐" placement="left">
     <div class="globalTools_item" :class="bgMusic?'removeMusic':'music'" @click="bgMusicChange()"></div>
    </el-tooltip>
    <el-dialog
      title="视角切换"
      :visible.sync="dialogVisible"
       width="65%">
      <el-row :gutter="8">
        <el-col style="margin: 12px 0;" :xs="12" :sm="12" :md="8" :lg="8" :xl="4" v-for="(item,index) in viewList" :key="index"  >
          <el-card shadow="always">
            <div class="cardItem" @click="viewChange(item)">
              <i :class="item.icon"></i>
              <span class="item_text">{{item.name}}</span>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
 data(){
  return {
     dialogVisible: false,
     viewList:[
      {
        icon:"el-icon-help",
        name:"正常视角",
        value:"skin_view_normal();",
      },
      {
        icon:"el-icon-picture-outline-round",
        name:"鱼眼视角",
        value:"skin_view_fisheye();",
      },
      {
        icon:"el-icon-view",
        name:"立体视角",
        value:"skin_view_stereographic();",
      },
      {
        icon:"el-icon-aim",
        name:"建筑视角",
        value:"skin_view_architectural();",
      },
      {
        icon:"el-icon-baseball",
        name:"Pannini视角",
        value:"skin_view_pannini();",
      },
      {
        icon:"el-icon-orange",
        name:"小行星视角",
        value:"skin_view_littleplanet();",
      }
     ]
  }
 },
computed: {
  ...mapGetters(['bgMusic'])
},
 methods:{
  qp(){
    this.$store.dispatch('globalEvent/qp')
  },
  xz(){
    this.$store.dispatch('globalEvent/xz')
  },
  vr(){
    this.$store.dispatch('globalEvent/vr')
  },
  bgMusicChange(){
    this.$store.dispatch('globalEvent/bgMusicChange')
  },
  viewChange(item){
   this.$store.dispatch('globalEvent/viewChange',item.value)
   this.dialogVisible=false
  }
 }
}
</script>


