<template>
   <el-dialog
      :visible.sync="dialogVisible"
      :width="dialog.width"
      :customClass="dialog.customClass"
      :title="dialog.title"
      :top="dialog.top"
      :append-to-body="dialog.appendToBody"
      :destroy-on-close="dialog.destroyOnClose"
      >
      <div v-if="dialog.type=='components'">
        <component :is="currentView" @changeDialogVisible="changeDialogVisible"></component>
      </div>
      <div v-else-if="dialog.type=='text'">
        <p>{{ dialog.val }}</p>
      </div>
      <div v-else-if="dialog.type=='img'">
        <el-carousel class="carousel">
          <el-carousel-item v-for="(item,index) in dialog.val" :key="index">
            <img :src="item">
            <!-- <el-image :src="item" fit="fit"></el-image> -->
          </el-carousel-item>
        </el-carousel>
      </div>
      <div v-else-if="dialog.type=='video'">
         <video :src="dialog.val" ref="videoRef" autoplay loop></video>
      </div>
   </el-dialog>
</template>


<script>
import { mapGetters } from 'vuex'
import HXGIntroduce from './component/HXGIntroduce.vue'
import RadioSelect from './component/RadioSelect.vue'

export default {
 data(){
  return {
     dialogVisible: false,
     currentView:null
  }
 },
components:{
  HXGIntroduce,
  RadioSelect
},
computed: {
  ...mapGetters(['dialog'])
},
watch: {
  //弹窗设置
  dialog: {
    handler(val) {
      this.currentView=val.val
      this.dialogVisible=true
    },
    deep:true
  },
  dialogVisible:{
    handler(val) {
      if(this.$refs.videoRef){
      if(val){
       setTimeout(() => {
         this.$refs.videoRef.currentTime = 0
         this.$refs.videoRef.play()
        }, 100)
      }else{
       setTimeout(() => {
         this.$refs.videoRef.pause()
         this.$refs.videoRef.currentTime = 0
        }, 100)
      } 
     }
    },
  }
},
 methods:{
 changeDialogVisible(val){
  this.dialogVisible=val
 }
 },
}
</script>

<style>
.el-dialog__title{
  font-size: 24px;
  line-height: 30px;
  font-weight: bold;
}
img{
  width: 100%;
  height: 100%;
}
p::before {
   content: " "; 
}
video {
  width: 100%;
  height: 100%;
}
</style>