<template>
  <div class="skin_wrapper">
    <div class="senceSelect">
      <i class="el-icon-menu menuIcon"  @click="drawer = true"></i>
      <span>场景选择</span>
    </div>
    <el-drawer
      :with-header="false"
      :visible.sync="drawer"
       direction="btt"
      :destroy-on-close="true"
      :show-close="false"
       size="15vh"
       class="drawer"
      >
      <div class="drawerContent">
        <div ref="senceList" class="senceList">
          <div class="item" :class="item.path==$store.state.globalEvent.currentScene?'selectItem':''" v-for="(item,idx) in panos" :key="idx" @click="changePanos(item.path)">
            <img :src="item.url" class="itemImg">
            <div class="itemText">{{item.name}}</div>
          </div>
        </div>
      </div>
    </el-drawer>
  </div>
</template>
 
<script>
  export default {
    data() {
      return {
        drawer: false,
        panos : [
          { name: "增城广场", url: "./vtour/panos/xiexfoxflxcg.tiles/thumb.jpg", path: "scene_xiexfoxflxcg" },
          { name: "何仙姑家庙", url: "./vtour/panos/wjvwhzxnrxucxgz.tiles/thumb.jpg", path: "scene_wjvwhzxnrxucxgz" },
          { name: "何仙姑家庙仙藤园", url: "./vtour/panos/wjvwhzxnrxucxgzwhzaxkxbt.tiles/thumb.jpg", path: "scene_wjvwhzxnrxucxgzwhzaxkxbt" },
          { name: "何仙姑家庙钟楼", url: "./vtour/panos/wjvwhzxnrxucxgzbsfyli.tiles/thumb.jpg", path: "scene_wjvwhzxnrxucxgzbsfyli" },
          { name: "小楼人家", url: "./vtour/panos/xwpyliwggxuc.tiles/thumb.jpg", path: "scene_xwpyliwggxuc" },
          { name: "小楼人家报德寺", url: "./vtour/panos/xwpyliwggxuc-yklxkdxvg.tiles/thumb.jpg", path: "scene_xwpyliwggxuc-yklxkdxvg" },
          { name: "小楼人家-八仙湖", url: "./vtour/panos/xwpyliwggxuc-xfrwhzyfw.tiles/thumb.jpg", path: "scene_xwpyliwggxuc-xfrwhzyfw" },
        ]
      };
    },
   watch:{
    drawer(val){
      if(val){
        this.$nextTick(()=>{
          this.initScroll()
          this.initDrag()
        })
      }
    }
   },
    methods: {
      initScroll() {
        let scrollWidth = 100
        let el = this.$refs.senceList;
        el.addEventListener('wheel', (event) => {
          let delta = event.wheelDelta
          if (delta > 0) {
             this.$refs.senceList.scrollLeft -= scrollWidth
           } else {
             this.$refs.senceList.scrollLeft += scrollWidth
          }
        })
      },
      initDrag() {
        let gapX, startX
        this.$refs.senceList.addEventListener('mousedown', e => {
          if (e.button === 0) {
            gapX = e.clientX
            startX = this.$refs.senceList.scrollLeft
            document.addEventListener('mousemove', move)
            document.addEventListener('mouseup', stop)
          }
          return false
        })
        const move=(e)=> {
          let left = e.clientX - gapX
          this.$refs.senceList.scrollLeft = startX - left
          return false
        }
        const stop=()=> {
          document.removeEventListener('mousemove', move)
          document.removeEventListener('mouseup', stop)
        }
      },
      changePanos(path){
        this.$store.dispatch('globalEvent/setScene', path)
        this.drawer=false
      }
    }
  };
</script>


