const state = {
  currentScene: 'scene_xiexfoxflxcg',
  fullscreen: false,
  rotate: false,
  vr: false,
  bgMusic: false,
  viewState: 'skin_view_normal();',
  dialog: {
    type: null,
    val: '',
    title: '',
    width: '80%',
    customClass: null,
    top: '15vh',
    appendToBody: true,
    destroyOnClose: true,
    dialogVisible: false
  }
}
const mutations = {
  SETSCENE(state, scene) {
    state.currentScene = scene
  },
  QP(state) {
    state.fullscreen = !state.fullscreen
  },
  XZ(state) {
    state.rotate = !state.rotate
  },
  VR(state) {
    state.vr = !state.vr
  },
  BGMUSICCHANGE(state) {
    state.bgMusic = !state.bgMusic
  },
  VIEWCHANGE(state, value) {
    state.viewState = value
  },
  SHOWDIALOG(state, obj) {
    const {
      type,
      val,
      title,
      width,
      customClass,
      top,
      appendToBody,
      destroyOnClose
    } = obj
    state.dialog.type = type
    state.dialog.val = val
    state.dialog.title = title
    state.dialog.width = width || "80%"
    state.dialog.customClass = customClass || null
    state.dialog.top = top || '15vh'
    state.dialog.appendToBody = appendToBody || true
    state.dialog.destroyOnClose = destroyOnClose || true
    state.dialog.dialogVisible = !state.dialog.dialogVisible
  }
}

const actions = {
  setScene({ commit }, name) {
    commit('SETSCENE', name)
  },
  qp({ commit }) {
    commit('QP')
  },
  xz({ commit }) {
    commit('XZ')
  },
  vr({ commit }) {
    commit('VR')
  },
  bgMusicChange({ commit }) {
    commit('BGMUSICCHANGE')
  },
  viewChange({ commit }, value) {
    commit('VIEWCHANGE', value)
  },
  showDialog({ commit }, obj) {
    commit('SHOWDIALOG', obj)
  }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
